import helper from '../../common/helper.js'
export default {
	data() {
		return {
			AwardArraylist: [{
					TaskName: "绑定手机号奖励",
					Reward: 0.5
				},
				{
					TaskName: "绑定银行卡奖励",
					Reward: 0.5
				},
				{
					TaskName: "绑定支付宝奖励",
					Reward: 0.5
				},
				{
					TaskName: "绑定身份证奖励",
					Reward: 0.5
				},
			
			],
			Dailylogin:{
				TaskName:"每日登陆奖励",
				}, // 每日登录
			chooseday:'',
			tishi:'领取'
		};
	},
	onLoad() {
		// this.award()
	},
	methods: {			
		award() {
			var States = this.Dailylogin.States;
			uni.showLoading({
					title: "加载中....",
					mask: true
				}),
				uni.request({
					url: helper.websiteUrl + 'reward/getrewardlist',
					method: 'GET',
					data: helper.postdata({
						"userid": helper.getstate().userid,
					}),
					success: res => {	
						if (res.data.code == 200) {	
							uni.hideLoading()
							this.Dailylogin = res.data.data.userinfo;     //每日登陆	
							this.AwardArraylist =res.data.data.baseinfo			//奖励列表数组	
						} else {
							helper.goout(res.data.info);
							uni.showToast({
								icon: 'none',
								title: res.data.info
							});
						}
					},
					fail: () => {
						uni.showToast({
							icon: 'none',
							title: '网络异常,请稍后重试'
						});
					}
				});
		},
		awards(e){
			uni.request({
				url: helper.websiteUrl + 'reward/savereward',
				method: 'GET',
				data: helper.postdata({
					"userid": helper.getstate().userid,
					"taskid":e.currentTarget.dataset.taskid
				}),
				success: res => {
					if (res.data.code == 200) {								
							uni.showToast({
								icon: 'none',
								title: '领取成功'
							});	
							this.Dailylogin.States = '2'
					} else {
						helper.goout(res.data.info);
						uni.showToast({
							icon: 'none',
							title: res.data.info
						});
					}
				},
				fail: () => {
					uni.showToast({
						icon: 'none',
						title: '网络异常,请稍后重试'
					});
				}
			});
		},
		Lqbut(e){
			if (e.currentTarget.dataset.states== '0') {
				uni.showToast({
					icon: 'none',
					title: '请'+e.currentTarget.dataset.taskname
				});	
			} else if(e.currentTarget.dataset.states== '2'){
				uni.showToast({
					icon: 'none',
					title: '已领取奖励'
				});
				this.tishi='已领取'
			}else if(e.currentTarget.dataset.states== '1'){
				this.awards(e)
				this.tishi='已领取'
			}
		}
	
	}
}
